import { OlympicDetail } from '@/types';
import axios, { AxiosResponse } from 'axios';

const OLYMPIC_API_BASE_URL = 'http://localhost:8080/api/althletes';

export default {
  getOlympic(): Promise<AxiosResponse<OlympicDetail[]>> {
    return axios.get<OlympicDetail[]>(OLYMPIC_API_BASE_URL);
  },
};
